const Yanychar = {}

Yanychar.makeNumber = function (day1, day2, day3, day4, day5, day6, day7) {
    let date = localStorage.getItem("date");
    const week = 60*60*24*7;
    if (date === null) {
        const now = Date.now();
        localStorage.setItem('date', now);
        localStorage.setItem('dateWeekEnd', now + week);
        return day1;
    } else {

    }
    date = Number(localStorage.getItem('date'))
    const dateNow = Number(localStorage.getItem('dateWeekEnd'))
    const dateCount = (dateNow - date) / week;
    let res = null
    switch (dateCount) {
        case 1:
            res = day1
            break
        case 2:
            res = day2
            break
        case 3:
            res = day3
            break
        case 4:
            res = day4
            break
        case 5:
            res = day5
            break
        case 6:
            res = day6
            break
        case 7:
            res = day7
            break
        default:
            res = day1
            break
    }
    return res
}

export default Yanychar
